footer {
    width: 100%;
    padding: 10px 20px;
    background-color: var(--secondary);
    color: #fff;
    text-align: center;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
    bottom: 0;
}

.footer-links a {
