/* Main container for the layout */
.game-container {
    display: flex; /*flex default*/
    align-items: center; /* Center content vertically */
    justify-content: center;
    gap: var(--gap-lg);
    height: 100%;
    width: auto;
    grid-template-rows: auto 1fr;
}

.button {
    padding: 10px 15px;
    font-size: 16px;
    cursor: pointer;
    margin: 5px;
}

.promotion-button {
    padding: 2px 4px;
    font-size: 16px;
    cursor: pointer;
    margin: 3px;
    background-color: rgba(0,0,0,0);
    border: none;
}

.promotion-image {
    max-width: 40px;
}

.promotion-button:hover {
    filter: drop-shadow(0px 0px 5px rgba(255, 255, 255, 1)) 
    drop-shadow(0px 0px 10px rgba(255, 255, 255, 0.8)) 
    drop-shadow(0px 0px 15px rgba(255, 255, 255, 0.6));
    transition: filter 0.01s ease-in-out;
}

.promotion-modal {
    position: absolute;
    top: 50%;
    left: 50%;
    max-width: 250px;
    transform: translate(-50%, -50%);
    padding: 5px 10px;
    border-radius: 8px;
    background: rgba(0,0,0,0.4);
}

.game-over-modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 5px 10px;
    border-radius: 8px;
    background: rgba(0,0,0,0.8);
    color: var(--text-inverse);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.text-display {
    text-align: center;
    padding: var(--gap-lg) var(--gap-break);
    background-color: var(--primary);
    color: var(--text-inverse);
    border-radius: var(--radius);
    width: 180px;
    overflow-wrap: break-word;
}

.modal-text-display {
    text-align: center;
    padding: var(--gap-lg) var(--gap-break);
    color: var(--text-inverse);
    border-radius: var(--radius);
    overflow-wrap: break-word;
    margin-left: 2px;
    margin-right: 2px;
    text-align: center;
    font-size: 1rem;
    font-weight: bold;
}

.evaluation-bar {
    width: 40px; /* Adjust width as needed */
    height: 100%; /* Match the height of the chessboard */
    display: flex;
    flex-direction: column;
    background-color: #ffffff; /* Background for the bar */
    border: 2px solid #000; /* Optional border */
    position: relative;
    margin: 0px;
}

.white-bar {
    background-color: #ffffff; /* White's color */
    width: 100%;
    transition: height 0.3s ease; /* Smooth height transition */
    padding: 1px;
}

.black-bar {
    background-color: #000000; /* Black's color */
    width: 100%;
    transition: height 0.3s ease; /* Smooth height transition */
    padding: 1px;
}

.game-ui {
    display: flex; /* Arrange children in a row */
    flex-direction: row; /* Ensure the direction is horizontal */
    width: 100%; /* Use the full width of the parent container */
    height: 80%;
    align-items: center; /* Center content vertically */
    justify-content: center;
}

.chessboard-panel {
    /* Allow explicit width control */
    height: 40%; /* Ensure columns take full height */
    width: 40%;
    display: flex;
    align-items: center; /* Center content vertically */
    justify-content: center;
}

.evaluation-panel {
    flex: 0 auto auto; /* Allow explicit width control */
    width: 20%;
    height: 80%; /* Ensure columns take full height */
    display: flex; /*flex default*/
    align-items: center; /* Center content vertically */
    justify-content: center;
    flex-direction: row;
    padding: 25px;
    margin: 25px;
}

.evaluation-column {
    flex: 0 0 auto; /* Allow explicit width control */
    height: 80%; /* Ensure columns take full height */
    display: flex;
    align-items: center; /* Center content vertically */
    justify-content: center;
    flex-direction: column;
    padding: 25px;
}

.info-panel {
    flex: 0 auto auto; /* Allow explicit width control */
    width: 20%;
    height: 80%; /* Ensure columns take full height */
    display: flex; /*flex default*/
    align-items: center; /* Center content vertically */
    justify-content: center;
    flex-direction: column;
    padding: 25px;
    margin: 25px;
}

.info-column {
    flex: 0 0 auto; /* Allow explicit width control */
    height: 80%; /* Ensure columns take full height */
    display: flex;
    align-items: center; /* Center content vertically */
    justify-content: center;
    flex-direction: column;
    padding: 25px;
}

.highlight {
    background-color: rgba(255, 255, 0, 0.3); /* Yellow highlight */
    border: 2px solid yellow;
}

.move-indicator {
    width: 25%; /* Adjust size as needed */
    height: 25%;
    border: 2px solid black;
    border-radius: 50%; /* Makes it a perfect circle */
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); /* Center the circle */
    pointer-events: none; /* Prevent interaction */
}

.capture-highlight {

    filter: drop-shadow(0px 0px 5px rgba(200, 0, 0, 1)) 
            drop-shadow(0px 0px 10px rgba(200, 0, 0, 0.8)) 
            drop-shadow(0px 0px 15px rgba(200, 0, 0, 0.6));
    transition: filter 0.01s ease-in-out;
}

.ai-thinking {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: white;
    font-size: 1.2rem;
    font-weight: bold;
    background: rgba(0, 0, 0, 0.7);
    padding: 10px 20px;
    border-radius: 8px;
}


.loader {
    border: 4px solid rgba(255, 255, 255, 0.3);
    border-top: 4px solid #fff;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    animation: spin 1s linear infinite;
    margin: auto;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}
